<template>
<!-- cityData.json -->
<!-- <my-picker @getsonaddr="getAddr"></my-picker> -->
<!--  -->
 <!-- getAddr(addr,state){
            this.addr1 = addr 
            this.state.addr1 =state
            console.log(this.addr1);
            console.log(this.state.addr1); 
        } -->
    <div>
       <div @click="showAddressPicker">
          <mt-field label=":" placeholder="" v-model="region"  readonly="readonly" :state="Sregion"></mt-field>
       </div>
     <!-- <input type="text" placeholder="" v-model="region" maxlength="80" readonly="readonly" @click="showAddressPicker" /> -->
       <mt-popup v-model="regionVisible" position="bottom">
         <div class="popbtn">
           <mt-button @click="cancle" plain size="small"></mt-button>
             <mt-button @click="define" plain type="primary" size="small"></mt-button>
         </div>
       <mt-picker :slots="myAddressSlots" valueKey="name" :visibleItemCount="5" @change="addressChange" :itemHeight="40" ref="pickCom"></mt-picker>
       </mt-popup>
   </div>
</template>
<script>

import threeLevelAddress from '../../assets/json/cityData.json'
export default {
    data() {
            return {
                Sregion:'error',
                yladdr:'',
                oldCityList: '',
                oldQuList: '',
                oldSlots: '',
                region: '', //
                province: '', //
                city: '', //
                county: '', //
                provinceCode: '', //
                cityCode: '', //
                countyCode: '', //

                regionVisible: false, //
                regionInit: false, //picker3

                //picker
                myAddressSlots: [           //
                    {
                        flex: 1,
                        values: this.getProvinceArr(), //
                        className: 'slot1',
                        textAlign: 'center'
                    },             //
                    {
                        divider: true,
                        content: '',
                        className: 'slot2'
                    },          //
                    {
                        flex: 1,
                        values: this.getCityArr(""),
                        className: 'slot3',
                        textAlign: 'center'
                    }, {
                        divider: true,
                        content: '',
                        className: 'slot4'
                    },          //
                    {
                        flex: 1,
                        values: this.getCountyArr("", ""),
                        className: 'slot5',
                        textAlign: 'center'
                    }
                ]
            }
        },
        watch: {
          region(newVal){
            if(newVal!=0){
              this.Sregion  = 'success'
              
              this.$emit('getsonaddr',this.region,this.Sregion)
              
            }else{
              this.Sregion  = 'error'
               this.$emit('getsonaddr',this.region,this.Sregion)
            }
          }
        },
        methods: {
            cancle() {
                this.regionVisible=false;
                this.region = this.yladdr

                },
                define() {
                  this.yladdr = this.region
                   this.$emit('getsonaddr',this.region,this.Sregion)
                    this.popupVisible = false;
                    // 
                    var valueArr = this.$refs.pickCom.getValues()
                    this.oldSlots = valueArr.concat();
                    this.oldCityList = this.$refs.pickCom.getSlotValues(2);
                    this.oldQuList = this.$refs.pickCom.getSlotValues(4);
                    var text = '';
                    this.oldSlots.forEach(item => {
                        text += item.name
                    });
                    this.regionVisible=false;
                    //
                    // console.log(text);
                },
                //
                showAddressPicker() {
                    this.regionVisible = true;
                },
                //pickerchange
                addressChange(picker, values) {
                    if (this.regionInit) {           //
                        this.region = values[0]["name"] + values[1]["name"] + values[2]["name"];
                        this.province = values[0]["name"];
                        this.city = values[1]["name"];
                        this.county = values[2]["name"];
                        this.provinceCode = values[0]["code"];
                        this.cityCode = values[1]["code"];
                        this.countyCode = values[2]["code"];

                                   //
                        picker.setSlotValues(1, this.getCityArr(values[0]["name"]));
                        picker.setSlotValues(2, this.getCountyArr(values[0]["name"], values[1]["name"]));
                    } else {
                        this.regionInit = true;
                    }
                },      //json
                getProvinceArr() {
                    let provinceArr = [];
                    threeLevelAddress.forEach(function(item) {
                        let obj = {};
                        obj.name = item.name;
                        obj.code = item.code;
                        provinceArr.push(obj);
                    });
                    return provinceArr;
                },        //json
                getCityArr(province) {
                    // console.log("" + province);
                    let cityArr = [];
                    threeLevelAddress.forEach(function(item) {
                        if (item.name === province) {
                            item.children.forEach(function(args) {
                                let obj = {};
                                obj.name = args.name;
                                obj.code = args.code;
                                cityArr.push(obj);
                            });
                        }
                    });
                    return cityArr;
                },      //json
                getCountyArr(province, city) {
                    let countyArr = [];
                    threeLevelAddress.forEach(function(item) {
                        if (item.name === province) {
                            item.children.forEach(function(args) {
                                if (args.name === city) {
                                    args.children.forEach(function(param) {
                                        let obj = {};
                                        obj.name = param.name;
                                        obj.code = param.code;
                                        countyArr.push(obj);
                                    })
                                }
                            });
                        }
                    });
                    return countyArr;
                },
        },
        mounted() { 
        
        }
}
</script>
<style scoped>
.popbtn {
    display: flex;
    justify-content: space-between;
 
}
 .popbtn  button {
        margin: 10px;
    }
.mint-popup-bottom {
    width: 100%!important;
    z-index: 5555;
}
</style>